/** @jsx React.DOM */var React = require('react');

var {IntlMixin, FormattedMessage} = require('react-intl');

var {Panel, Table} = require('react-bootstrap');

module.exports = React.createClass({
  mixins: [IntlMixin],

  render: function () {
    return (
      <div className="container">
        <Panel header={<h3>{this.getIntlMessage('contacts')}</h3>}>
          <Table fill>
            <thead>
              <tr><th>{this.getIntlMessage('telephone')}</th><th></th></tr>
            </thead>
            <tbody>
              <tr>
                <td>{this.getIntlMessage('office')}</td>
                <td>{this.getIntlMessage('iotok.phone')}</td>
              </tr>
            </tbody>
            <thead>
              <tr><th>{this.getIntlMessage('email')}</th><th></th></tr>
            </thead>
            <tbody>
              <tr>
                <td>{this.getIntlMessage('support')}</td>
                <td><a href={'mailto:'+this.getIntlMessage('iotok.email.support')}>{this.getIntlMessage('iotok.email.support')}</a></td>
              </tr>
              <tr>
                <td>{this.getIntlMessage('sales')}</td>
                <td><a href={'mailto:'+this.getIntlMessage('iotok.email.sales')}>{this.getIntlMessage('iotok.email.sales')}</a></td>
              </tr>
              <tr>
                <td>{this.getIntlMessage('partnership')}</td>
                <td><a href={'mailto:'+this.getIntlMessage('iotok.email.partnership')}>{this.getIntlMessage('iotok.email.partnership')}</a></td>
              </tr>
            </tbody>
          </Table>
        </Panel>
      </div>
    );
  }
});



